import React from "react"
import { injectIntl, Link, FormattedMessage } from "gatsby-plugin-intl"

const En = ({ intl }) => {
  return (
    <React.Fragment>
      <Link to="/">
        {intl.formatMessage({ id: "title" })}
        {/* OR <FormattedMessage id="go_page2" /> */}
      </Link>
    </React.Fragment>
  )
}
export default injectIntl(En)